<script>
	let nombre = '';

	function handleContinuar(tipo) {
		console.log(tipo);

		if (nombre.length === 0) {
			alert('Debes proporcionar el nombre de quien juega.');
		} else {
			if (tipo === 1) {
				console.log('Continuar como donna ...');
			} else {
				console.log('Continuar como uomo ...');
			}
		}
	}
</script>

<main>
	<div class="container">
		<div class="row">
			<div class="col center title">
				¿Quién juega?
			</div>
		</div>
		<div class="row">
			<div class="col center">
				<input type="text" class="form-control" bind:value={nombre}>
			</div>
		</div>
		<div class="row">
			<div class="col center title">
				Selecciona un avatar para continuar
			</div>
		</div>
		<div class="row">
			<div class="col center">
				<img src="/ypintor/assets/images/donna.png" width="135" height="135" alt="Donna" on:click={e => handleContinuar(1)}>
				&nbsp;&nbsp;&nbsp;
				<img src="/ypintor/assets/images/uomo.png" width="135" height="135" alt="Uomo" on:click={e => handleContinuar(2)}>
			</div>
		</div>
	</div>
</main>

<style>
	.title {
		margin-top: 2rem;
		margin-bottom: 0.5rem;
		font-size: 24pt;
		font-weight: 400;
	}

	.center {
		text-align: center;
	}
</style>